<template>
  <div v-if="!collection" class="tips">暂无内容</div>
  <div class="list-content" v-else>
    <div class="list-item" v-for="item in collection">
      <img class="pic" :src="`${imgUrl}image/article/feature/${item.feature}`"/>
      <div class="article-info">
        <router-link class="title" :to="{path:'/detail',query:{id:item.id}}">{{item.title}}</router-link>
        <p class="desc">{{item.content.match(reg).join('')}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "collection",
    data() {
      return {
        imgUrl: process.env.BASE_URL,
        collection: '',
        cur_user: '',
        reg: /[\u4e00-\u9fa5]/g
      }
    },
    created(){
      this.cur_user = this.$store.getters.getCurUser
      this.$axios('/blog/api/getCollection?id='+this.cur_user.id).then((res) => {
        this.collection = res.data
      })
    },
    mounted() {

    }

  }
</script>

<style scoped>
  .tips{
    font-size: 34px;
    font-weight: 700;
    color: #969696;
  }

  .list-content {
    padding-top: 30px;
    padding-right: 30px;
    width: 100%;
    float: left;
    box-sizing: border-box;
  }

  .list-item {
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid #dcdcdc;
    height: 105px;
    box-sizing: border-box;
  }

  .title {
    line-height: 27px;
    font-size: 18px;
    font-weight: 700;
    color: #333;
    cursor: pointer;
  }

  .desc {
    line-height: 24px;
    font-size: 13px;
    color: #999;
  }

  .pic {
    display: block;
    width: 16%;
    height: 100px;
    float: right;
    border-radius: 10px;
  }

  .article-info {
    float: left;
    width: 80%;
  }

  a{
    text-decoration: none;
  }

</style>
